@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic:wght@100;200;300;400;500;600;700&display=swap');

:root {
  --bg-color: #161519;
  --bg-light-color: #1e1c22;
  --weight-color: #fff;

  --main-color: 6, 216, 137;
  --main-color-opacity-100: rgba(var(--main-color), 1);
  --main-color-opacity-50: rgba(var(--main-color), 0.5);
  --main-color-opacity-35: rgba(var(--main-color), 0.35);
  --main-color-opacity-20: rgba(var(--main-color), 0.2);
  --main-color-opacity-1: rgba(var(--main-color), 0.1);
  --main-color-opacity-05: rgba(var(--main-color), 0.05);
}

* {
  border: none;
  box-sizing: border-box;
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  list-style: none;
  margin: 0;
  outline: none;
  padding: 0;
  text-decoration: none;
  scroll-behavior: smooth;
  cursor: url('../src/assest/cursor-icon.png'), auto !important;
}

::-webkit-scrollbar {
  width: 12px !important;
}

::-webkit-scrollbar-thumb {
  background: var(--main-color-opacity-100) !important;
  border-radius: 12px;
  transition: all 0.3s ease;
  box-shadow: none !important;
}

::-webkit-scrollbar-track {
  background: var(--bg-light-color);
}

body {
  margin: 0;
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  background-color: var(--bg-color);
  overflow-x: clip;
  color: var(--weight-color);
  scroll-behavior: smooth;
  cursor: url('../src/assest/cursor-icon.png'), auto !important;

}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

::selection {
  -webkit-text-fill-color: initial !important;
  background-clip: initial !important;
  -webkit-background-clip: initial !important;
  background-color: var(--main-color-opacity-100);
  background-image: none !important;
  color: #f5f5f5 !important;
}

a {
  color: var(--weight-color);
}

section {
  position: relative;
}

/* loader */
.loader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 5555;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(20px);
  background: rgb(255 255 255 / 20%);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.loader.none {
  display: none;
}

.pl {
  width: 6em;
  height: 6em;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.pl__ring {
  animation: ringA 2s linear infinite;
}

.pl__ring--a {
  stroke: var(--main-color-opacity-100);
}

.pl__ring--b {
  animation-name: ringB;
  stroke: var(--main-color-opacity-20);
}

.pl__ring--c {
  animation-name: ringC;
  stroke: var(--main-color-opacity-35);
}

.pl__ring--d {
  animation-name: ringD;
  stroke: var(--main-color-opacity-50);
}

/* Animations */
@keyframes ringA {

  from,
  4% {
    stroke-dasharray: 0 660;
    stroke-width: 20;
    stroke-dashoffset: -330;
  }

  12% {
    stroke-dasharray: 60 600;
    stroke-width: 30;
    stroke-dashoffset: -335;
  }

  32% {
    stroke-dasharray: 60 600;
    stroke-width: 30;
    stroke-dashoffset: -595;
  }

  40%,
  54% {
    stroke-dasharray: 0 660;
    stroke-width: 20;
    stroke-dashoffset: -660;
  }

  62% {
    stroke-dasharray: 60 600;
    stroke-width: 30;
    stroke-dashoffset: -665;
  }

  82% {
    stroke-dasharray: 60 600;
    stroke-width: 30;
    stroke-dashoffset: -925;
  }

  90%,
  to {
    stroke-dasharray: 0 660;
    stroke-width: 20;
    stroke-dashoffset: -990;
  }
}

@keyframes ringB {

  from,
  12% {
    stroke-dasharray: 0 220;
    stroke-width: 20;
    stroke-dashoffset: -110;
  }

  20% {
    stroke-dasharray: 20 200;
    stroke-width: 30;
    stroke-dashoffset: -115;
  }

  40% {
    stroke-dasharray: 20 200;
    stroke-width: 30;
    stroke-dashoffset: -195;
  }

  48%,
  62% {
    stroke-dasharray: 0 220;
    stroke-width: 20;
    stroke-dashoffset: -220;
  }

  70% {
    stroke-dasharray: 20 200;
    stroke-width: 30;
    stroke-dashoffset: -225;
  }

  90% {
    stroke-dasharray: 20 200;
    stroke-width: 30;
    stroke-dashoffset: -305;
  }

  98%,
  to {
    stroke-dasharray: 0 220;
    stroke-width: 20;
    stroke-dashoffset: -330;
  }
}

@keyframes ringC {
  from {
    stroke-dasharray: 0 440;
    stroke-width: 20;
    stroke-dashoffset: 0;
  }

  8% {
    stroke-dasharray: 40 400;
    stroke-width: 30;
    stroke-dashoffset: -5;
  }

  28% {
    stroke-dasharray: 40 400;
    stroke-width: 30;
    stroke-dashoffset: -175;
  }

  36%,
  58% {
    stroke-dasharray: 0 440;
    stroke-width: 20;
    stroke-dashoffset: -220;
  }

  66% {
    stroke-dasharray: 40 400;
    stroke-width: 30;
    stroke-dashoffset: -225;
  }

  86% {
    stroke-dasharray: 40 400;
    stroke-width: 30;
    stroke-dashoffset: -395;
  }

  94%,
  to {
    stroke-dasharray: 0 440;
    stroke-width: 20;
    stroke-dashoffset: -440;
  }
}

@keyframes ringD {

  from,
  8% {
    stroke-dasharray: 0 440;
    stroke-width: 20;
    stroke-dashoffset: 0;
  }

  16% {
    stroke-dasharray: 40 400;
    stroke-width: 30;
    stroke-dashoffset: -5;
  }

  36% {
    stroke-dasharray: 40 400;
    stroke-width: 30;
    stroke-dashoffset: -175;
  }

  44%,
  50% {
    stroke-dasharray: 0 440;
    stroke-width: 20;
    stroke-dashoffset: -220;
  }

  58% {
    stroke-dasharray: 40 400;
    stroke-width: 30;
    stroke-dashoffset: -225;
  }

  78% {
    stroke-dasharray: 40 400;
    stroke-width: 30;
    stroke-dashoffset: -395;
  }

  86%,
  to {
    stroke-dasharray: 0 440;
    stroke-width: 20;
    stroke-dashoffset: -440;
  }
}

/* loader */

.container {
  margin: 0 auto;
  max-width: 1200px;
  width: 90%;
  /* margin-top: 8rem; */
}

header {
  -webkit-backdrop-filter: blur(16px);
  backdrop-filter: blur(16px);
  /* background: #00000061; */
  top: 0;
  padding: 10px;
  position: fixed;
  width: 100%;
  z-index: 1000;
}

header.active {
  background: #00000061;
}

header::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 80%;
  background: linear-gradient(90deg, rgba(2, 0, 36, 0), var(--main-color-opacity-100) 49%, rgba(0, 212, 255, 0));
  height: 1px;
  transform: translateX(-50%);
}

header .container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: auto;
  position: relative;
}

header .links {
  align-items: center;
  display: flex;
  gap: 20px;
}

header .logo img {
  width: 100px;
}

header .links li {
  position: relative;
}

header .links a {
  font-size: 1.5rem;
  transition: all .3s ease-in-out;
}

header .links a.size-about {
  font-size: 1.8rem;
}

header .links a:hover {
  color: var(--main-color-opacity-100);
}

header .links a.active {
  color: var(--main-color-opacity-100);
  font-weight: bold;
  font-size: 2rem;
}

header .menu {
  display: none;
}


.button {
  height: 60px;
  width: 150px;
  position: relative;
  /* background-color: var(--main-color-opacity-100); */
  background: linear-gradient(45deg, var(--main-color-opacity-100), #313035, transparent);
  cursor: url('../src/assest/cursor-icon.png'), auto !important;
  /* border: 2px solid #252525; */
  overflow: hidden;
  border-radius: 1rem;
  color: #333;
  transition: all 0.5s ease-in-out;
}

header .btn-txt {
  font-size: 1.2rem;
}

.btn-txt {
  z-index: 1;
  font-weight: 800;
  font-size: 1.2rem;
}

.type1::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.5s ease-in-out;
  background-color: #333;
  border-radius: 30px;
  visibility: hidden;
  height: 10px;
  width: 10px;
  z-index: -1;
}

.button:hover {
  box-shadow: 1px 1px 200px var(--main-color-opacity-50);
  color: #fff;
  border: none;
}

.type1:hover::after {
  visibility: visible;
  transform: scale(100) translateX(2px);
}

/* start home */
.home {
  height: 100vh;
  /* background-image: linear-gradient(#1e1c22, #1e1c22); */
  /* background-image: linear-gradient(180deg, #605FE5 0%, #4B4AF2 100%); */
  background-image: linear-gradient(#595959, #1e1c22);
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}

.home .blur1 {
  position: absolute;
  width: 25rem;
  height: 31rem;
  border-radius: 50%;
  left: -19rem;
  background: var(--main-color-opacity-35);
  opacity: .1;
  box-shadow: 55px -7px 52px 0px var(--main-color-opacity-35);
}

.home .blur2 {
  position: absolute;
  width: 25rem;
  height: 31rem;
  border-radius: 50%;
  right: -19rem;
  background: var(--main-color-opacity-35);
  opacity: .1;
  box-shadow: -55px -7px 52px 0px var(--main-color-opacity-35);
  bottom: -5rem;
}

.home .container {
  margin-top: 17rem;
  /* margin-top: 11rem; */
}

.home .social {
  position: absolute;
  right: 60px;
  z-index: 99;
  top: 50%;
  transform: translateY(-50%);
}

.home .social ul {
  gap: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.home .social li {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.home .social li a {
  font-size: 20px;
  color: rgba(255, 255, 255, .7);
