  width: 2rem;
}

.home .social li a:hover {
  color: var(--main-color-opacity-100);
}

.home .content {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 7rem;
  /* margin-bottom: 3rem; */
}

.home .content .text-area {
  flex: 0 0 auto;
  width: 47%;
  margin: auto;
}

.home .content .image-area {
  flex: 0 0 auto;
  width: 47%;
}

.home .content .text-area .text {
  max-width: 740px;
  width: 100%;
}

.home .content .text-area .text h1 {
  font-weight: 700;
  font-size: 50px;
  line-height: 1.3;
  text-transform: capitalize;
  color: var(--white-color);
  margin-bottom: 30px;
}

.home .content .text-area .text h1 span {
  color: var(--main-color-opacity-100);
  position: relative;
  font-size: 50px;
}

