  content: url("../src/assest/text-bg.png");
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -35%);
  opacity: .2;
  z-index: -1;
} */

.home .content .text-area .text p {
  font-weight: 400;
  font-size: 20px;
  line-height: 35px;
  color: #e4e4e4;
  margin-bottom: 55px;
}

.home .content .text-area .btns {
  display: flex;
  align-items: center;
  gap: 60px;
}


.btn.two {
  font-weight: 500;
  font-size: 15px;
  letter-spacing: .03em;
  text-transform: uppercase;
  color: var(--white-color);
  display: flex;
  align-items: center;
  transition: .5s;
}

.btn.two svg {
  fill: var(--main-color-opacity-100);
}

.btn.two .btn-content {
  margin-left: -40px;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: .5s;
  z-index: 1;
