}

.btn.two i {
  transition: .3s;
  transform: rotate(45deg);
}

.btn.two:hover i {
  transform: rotate(90deg);
}

.home .content .image-area {
  flex: 0 0 auto;
  width: 50%;
}

.home .content .image-area .image {
  width: 100%;
  position: relative;
  animation: landing_zoom 6s infinite;

}


.home .content .image-area .image img {
  max-width: 945px;
  width: 100%;
}

.home .reviews {
  align-items: center;
  display: flex;
  text-align: center;
  padding: 1rem;

}

.home .reviews .icon {
  margin: auto;
  font-size: 2rem;
  fill: rgba(var(--main-color), 1);
}

.home .reviews .icon.dollar {
  font-size: 2rem;
  fill: rgba(var(--main-color), 1);
}

.home .reviews .icon i {
  color: var(--main-color-opacity-100);
  font-size: 2rem;
}

.home .reviews .counter {
  padding: 0 30px;
  width: 100%;
}

.home .reviews .counter ul {
  margin: 0;
  padding: 0;
  list-style: none;
  -moz-columns: 4;
  columns: 3;
  gap: 0;
}

.home .reviews .counter ul li {
  display: flex;
  justify-content: center;
  position: relative;
}

.home .reviews .counter .single-counter::after {
  content: "";
  width: 2px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  background-color: rgba(255, 255, 255, .1);
}

.home .reviews .counter .single-counter .number {
  display: flex;
  align-items: baseline;
}

.home .reviews .counter .single-counter p {
  font-weight: 400;
  font-size: 30px;
  letter-spacing: .02em;
  text-transform: capitalize;
  color: #b5b5b5;
  padding-top: 3px;
}

.home .reviews .counter .single-counter .number .counter {
  margin-bottom: 0;
  font-weight: 600;
  font-size: 38px;
  text-transform: capitalize;
  color: var(--white-color);
}

.home .reviews .counter .single-counter .number .counter span {
  font-weight: 500;
  font-size: 20px;
  text-transform: capitalize;
  color: var(--white-color);
  display: inline-block;
  margin-left: 5px;
}

/* end home */

/* about */
