  letter-spacing: .01em;
  text-transform: uppercase;
  color: var(--main-color-opacity-05);
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  line-height: 1;
}

.title p {
  text-align: center;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 60px;
  letter-spacing: .01em;
  color: var(--white-color);
}

.about {
  position: relative;
  padding-top: 5rem;
  padding-bottom: 3rem;
}

.about .about-content {
  text-align: center;
  margin-top: 2rem;
}

.about .image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 2px solid var(--main-color-opacity-100);
  box-shadow: 0px 0px 15px var(--main-color-opacity-35);
}

.about .text {
