  margin-right: auto;
}

.about .swiper_container {
  user-select: none;
}

.about .swiper_container img {
  /* width: 100%; */
}



/* about */

/* certificates */
.certificates {
  padding-top: 5rem;
  padding-bottom: 3rem;
  position: relative;
  background-color: var(--bg-light-color);
}

.certificates .exit,
.projects .exit,
.contact .exit {
  position: absolute;
  right: 5%;
  top: 5%;
}

.certificates .menu,
.projects .menu,
.contact .menu {
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  right: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: .5rem;
}

.certificates .menu span,
.projects .menu span,
.contact .menu span {
  width: 3rem;
  height: 4px;
  background: var(--weight-color);
  border-radius: 1rem;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.certificates .menu.active .span1,
.projects .menu.active .span1,
.contact .menu.active .span1 {
  transform: rotate(45deg);
}

.certificates .menu.active .span2,
.projects .menu.active .span2,
.contact .menu.active .span2 {
  display: none;
}

.certificates .menu.active .span3,
.projects .menu.active .span3,
.contact .menu.active .span3 {
  transform: rotate(120deg);
}

.certificates .cards {
  display: grid;
  gap: 1rem;
  column-gap: 6rem;
  grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
  width: 87%;
  margin: auto;
  margin-top: 2rem;
}

.certificates .cards .card {
  align-items: center;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  background: hsla(0, 0%, 100%, .1);
  border: 2px solid #615f5f;
  border-radius: 10px;
  box-shadow: 0 0 35px 0 rgba(0, 0, 0, .1);
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
  transition: .3s ease-in-out;
  width: 300px;
  height: 250px;
  z-index: 2;
  margin: auto;
  cursor: url('../src/assest/cursor-icon.png'), auto !important;
  position: relative;
}

.certificates .cards .card::before {
  position: absolute;
  content: '';
  width: 5rem;
  height: 5rem;
  top: -36px;
  left: 0;
  border-radius: 50%;
  background: var(--main-color-opacity-100);
  z-index: -1;
}

.certificates .cards .card::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgba(var(--main-color), 0.1);
  z-index: 30;
}

.certificates .cards .card:hover:after {
  width: 0;
}

.certificates .cards .card:hover img {
  scale: 1.1;
}

.certificates .cards .card img {
  width: 250px;
  height: 200px;
  transition: .3s ease;
  border-radius: .5rem;
}

.certificates .btn.two {
  font-weight: 500;
  font-size: 15px;
  letter-spacing: .03em;
  text-transform: uppercase;
  color: var(--white-color);
  display: flex;
  align-items: center;
  transition: .5s;
  justify-content: end;
  padding: 3rem 5px;
  width: 275px;
  margin-left: auto;
}

.certificates .btn.two .btn-content {
  margin-left: -40px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: .5s;
  z-index: 1;
}

/* certificates */

/* projects */
.projects {
  padding-top: 5rem;
  padding-bottom: 3rem;
  position: relative;
  /* background: var(--bg-light-color); */
}

.service {
  background: var(--bg-light-color);
}

.projects .btn.two {
  font-weight: 500;
  font-size: 15px;
  letter-spacing: .03em;
  text-transform: uppercase;
  color: var(--white-color);
  display: flex;
  align-items: center;
  transition: .5s;
  justify-content: end;
  padding: 0rem 5px 1rem;
  width: 275px;
  margin-left: auto;
}

.projects .btn.two .btn-content {
  margin-left: -40px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: .5s;
  z-index: 1;
}

.projects .cards {
  display: grid;
  gap: 1rem;
  column-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  width: 97%;
  margin: 2rem auto;
}

.projects .cards .card {
  position: relative;
  max-width: 412px;
  width: 100%;
  height: 300px;
  margin: auto;
  margin-right: 30px;
  overflow: hidden;
  padding: 1rem;
  box-shadow: 0 0 2px 0px rgba(var(--main-color), 0.35);
  transition: all .4s ease-in-out;
  cursor: url('../src/assest/cursor-icon.png'), auto !important;
  z-index: 2;
  margin: auto;
}

.projects .cards .card::before {
  position: absolute;
  content: '';
  width: 155px;
  height: 155px;
  background-color: var(--main-color-opacity-100);
  opacity: .2;
  border-radius: 50%;
  bottom: -2rem;
  right: -2rem;
  transition: all .3s ease-in-out;
  z-index: 1;
}

.projects .cards .card:hover i {
  transform: rotate(90deg);
}

.projects .cards .card:hover::before {
  width: 110%;
  height: 115%;
  opacity: .1;
  border-radius: 0%;
}

.projects .cards .card::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url(../src/assest/project.png), linear-gradient(#161519, #161519);
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
  transition: .5s;
}

.projects .cards .card h2 {
  font-weight: 700;
  font-size: 50px;
  letter-spacing: .03em;
  color: rgba(255, 255, 255, .08);
  margin-bottom: 0;
  /* margin-top: 2rem; */
  margin-left: 1rem;
}

.projects .cards .card h3 {
  font-weight: 600;
  font-size: 27px;
  letter-spacing: .02em;
  text-transform: capitalize;
  color: var(--weight-color);
  margin-top: 1rem;
  margin-left: 1rem;
}

.projects .cards .card p {
  font-weight: 300;
  font-size: 14px;
  line-height: 30px;
  letter-spacing: .03em;
  color: #b5b5b5;
  margin-bottom: 30px;
  margin-left: 1rem;
  height: 6rem;
}

.projects .cards .card a {
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.projects .cards .card i {
  transition: all .4s ease-in-out;
}

.projects .img-swiper {
  width: 100%;
  max-width: 13rem !important;
  max-height: 10rem !important;
  border-radius: 0.5rem;
}

.services .cards .card {
  height: 250px;
  text-align: center;
}

.services .cards .card p {
  font-weight: 900;
  font-size: 17px;
  line-height: 30px;
  letter-spacing: .03em;
  color: #b5b5b5ad;
  margin-bottom: 30px;
  margin-left: 1rem;
  height: 4rem;
}


.projects form .input-box .input-field {
  position: relative;
  width: 100%;
  margin: .8rem 0;
  display: flex;
  align-items: center;
  margin: auto;
}

.projects form .input-box .input-field input {
  width: 100%;
  height: 100%;
  padding: 1.5rem;
  padding-left: 3rem;
  font-size: 1rem;
  border-radius: .6rem;
  border: 2px solid rgba(var(--main-color), 0.35);
  color: #737373;
  background: transparent;
  position: relative;
  z-index: 2;
}

.projects form .icon {
  position: absolute;
  left: 1rem;
  font-size: 2rem;
  color: var(--main-color-opacity-100);
}

.services .btns {
  text-align: center;
  padding-top: 1rem;
}

/* projects */

/* memories */
.memories {
  /* background-color: var(--bg-light-color); */
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.memories .swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
  user-select: none;
}

.memories .swiper-slide {
  background-position: center;
  background-size: cover;
  width: 400px;
  height: 400px;
  border-radius: 1rem;
}

.memories .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
}

.memories .swiper-pagination-bullet-active {
  background-color: var(--main-color-opacity-100) !important;
  border-radius: 1rem;
}

.swiper-3d .swiper-slide-shadow-left {
  border-radius: 1rem;
  background-image: linear-gradient(45deg, var(--main-color-opacity-100), transparent) !important;
}

.swiper-3d .swiper-slide-shadow-right {
  border-radius: 1rem;
  background-image: linear-gradient(45deg, var(--main-color-opacity-100), transparent) !important;
}

/* memories */

/* contact */
.contact {
  background-color: var(--bg-light-color);
  padding-top: 5rem;
  padding-bottom: 5rem;
  min-height: 100vh;
}

.contact-content {
  position: relative;
  padding-top: 1rem;
}

.contact-content .contact-social {
  position: absolute;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  width: 55px;
  align-items: center;
  text-align: center;
  justify-content: center;
  gap: 1rem;
}

.contact-content .contact-social i {
  position: absolute;
  color: var(--weight-color);
}

.contact-content .contact-social .btn-sosial .hover {
  transition: all .3s ease-in-out;
  opacity: 0;
  left: 5rem;
  width: 100%;
  font-size: 10rem;
}

.contact-content .contact-social .btn-sosial:not(.hover):hover .hover {
  position: absolute;
  color: var(--weight-color);
  width: 100%;
  font-size: 7rem;
  z-index: -1;
  opacity: .2;
}

.contact-content .contact-social span {
  /* width: 50px;
  height: 50px; */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* background: var(--main-color-opacity-100); */
  border-radius: .5rem;
}

.contact form {
  max-width: 50rem;
  margin: 0 auto;
  text-align: center;
}

.contact form input:focus {
  color: var(--weight-color) !important;
}

.contact form .input-box {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contact form .icon {
  position: absolute;
  left: 1rem;
  font-size: 1.5rem;
  color: var(--main-color-opacity-100);
}

.contact form .icon.message {
  margin-top: 1.5rem;
}

.contact form .input-box .input-field {
  position: relative;
  width: 49%;
  margin: .8rem 0;
  display: flex;
  align-items: center;
}

.contact form .textarea-field {
  position: relative;
  margin: .8rem 0 2.7rem;
  display: flex;
}

.contact form .textarea-field textarea {
  resize: none;
}


.contact form .input-box .input-field input,
.contact form .textarea-field textarea {
  width: 100%;
  height: 100%;
  padding: 1.5rem;
  padding-left: 3rem;
  font-size: 1rem;
  border-radius: .6rem;
  border: 2px solid rgba(var(--main-color), 0.35);
  color: #737373;
  background: transparent;
  position: relative;
  z-index: 2;
}

.contact form .input-box .input-field input::placeholder,
.services form .input-box .input-field input::placeholder,
.contact form .textarea-field textarea::placeholder {
  color: var(--text-color);
}

.contact .btn {
  height: 60px;
  width: 150px;
  position: relative;
  background-color: var(--main-color-opacity-100);
  cursor: url('../src/assest/cursor-icon.png'), auto !important;
  /* border: 2px solid #252525; */
  overflow: hidden;
  border-radius: 1rem;
  color: #333;
  transition: all 0.5s ease-in-out;
}

.contact .btn-sosial {
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: transparent;
  position: relative;
  /* overflow: hidden; */
  border-radius: 7px;
  cursor: url('../src/assest/cursor-icon.png'), auto !important;
  transition: all .3s;
}

.contact .svgContainer {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  backdrop-filter: blur(4px);
  letter-spacing: 0.8px;
  border-radius: 10px;
  transition: all .3s;
  border: 1px solid rgba(156, 156, 156, 0.466);
}

.contact .BG-social {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(var(--main-color-opacity-100), #313035, transparent);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#f09433', endColorstr='#bc1888', GradientType=1);
  z-index: -1;
  border-radius: 9px;
  pointer-events: none;
  transition: all .3s;
}


.contact .btn-sosial:hover .BG-social {
  transform: rotate(35deg);
  transform-origin: bottom;
}

.contact .btn-sosial:hover .svgContainer {
  background-color: rgba(156, 156, 156, 0.466);
}

.pay {
  text-align: center;
  padding-top: 2rem;
}

.pay .boxs {
  display: flex;
  justify-content: center;
}

.pay h3 {
  font-size: 1.5rem;
  color: var(--main-color-opacity-100);
  padding: 1rem;
}

/* contact */

/* service */
.service .card-service .img {
  align-items: center;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  background: hsla(0, 0%, 100%, .1);
  border: 2px solid #615f5f;
  border-radius: 10px;
  box-shadow: 0 0 35px 0 rgba(0, 0, 0, .1);
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
  transition: .3s ease-in-out;
  width: 100%;
  max-width: 40rem;
  z-index: 2;
  margin: auto;
  cursor: url(data:image/png;base64,), auto !important;
  position: relative;
  margin-top: 2rem;
  padding: 2rem;
  text-align: center;
}

.service .card-service .img::before {
  position: absolute;
  content: '';
  width: 7rem;
  height: 7rem;
  top: -36px;
  left: -01rem;
  border-radius: 50%;
  background: var(--main-color-opacity-100);
  z-index: -1;
}

.service .img img {
  width: 100%;
  max-width: 35rem;
  border-radius: .5rem;
}

.service .content {
  width: 100%;
  max-width: 35rem;
  text-align: center;
  margin: auto;
  padding-top: 2rem;
}

.service .content h3 {
  font-size: 2rem;
  color: var(--main-color-opacity-100);
}

.service .content p {
  font-size: 1.4rem;
}

.service .box {
  margin-bottom: 2rem;
}

.service .priceP {
  line-height: 2.5;
}

.service .priceSpan {
  background: var(--main-color-opacity-20);
  border-radius: .3rem;
  padding: 0.5rem;
  margin-bottom: 1rem;
}

/* service */



/* footer */
.footer {
  background-image: url(../src/assest/footer-bg.png), linear-gradient(#1e1c22, #1e1c22);
  padding: 2rem 2rem 1rem;
  /* margin-top: 5rem; */
  position: relative;
  overflow: hidden;
}

.footer::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  width: 80%;
  background: linear-gradient(90deg, rgba(2, 0, 36, 0), var(--main-color-opacity-100) 49%, rgba(0, 212, 255, 0));
  height: 1px;
  transform: translateX(-50%);
}

.footer .footer-top {
  text-align: center;
  padding-bottom: 2rem;
}

.footer img {
  width: 100%;
  max-width: 200px;
  text-align: center;
}

.footer .footer-bottom {
  padding: 1rem 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-top: 1px solid #494949;
  border-radius: 0.2rem;
}

.footer .footer-bottom p {
  margin-bottom: 0;
  font-weight: 400;
  font-size: 14px;
  color: #adadad;
}

.footer .footer-bottom p a {
  font-size: 17px;
}

.footer .footer-bottom p a:hover {
  color: var(--main-color-opacity-100);
}

/* footer */

/* media */
@media (max-width:1375px) {
  .home {
    height: auto;
  }

  .home .container {
    margin-top: 10rem;
  }

  .home .content {
    justify-content: center;
    margin-bottom: 0;
  }

  .home .content .text-area {
    width: 100%;
  }

  .home .content .text-area .text {
    text-align: center;
    margin: auto;
  }

  .home .content .text-area .btns {
    justify-content: center;
  }

  .contact form {
    max-width: 45rem;
  }

  .contact-content .contact-social {
    right: 10%;
  }

}

@media (max-width:1200px) {
  .contact-content .contact-social {
    right: 47%;
    top: 115%;
    flex-direction: row;
  }

  .contact-content {
    padding-top: 2rem;
  }

  .contact-content .contact-social .btn-sosial .hover {
    right: -1.5rem;
    top: 4.5rem;
    font-size: 8rem;
  }

  .footer {
    margin-top: 12rem;
  }
}

@media (max-width:900px) {
  .loader img {
    width: 50% !important;
  }

  header .container.active {
    flex-direction: column;
    height: 40vh;
  }

  header .container ul {
    top: -7rem;
    position: absolute;
  }

  header .container.active ul {
    flex-direction: column;
    position: static;
    line-height: 1;
  }

  header .container .button {
    width: 120px;
    height: 45px;
    top: -5rem;
    position: absolute;
  }

  header .container.active .button {
    position: static;
  }

  header .container .menu {
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: .5rem;
  }

  header .container .menu.active .span1 {
    transform: rotate(45deg);
  }

  header .container .menu.active .span2 {
    display: none;
  }

  header .container .menu.active .span3 {
    transform: rotate(120deg);
  }

  header .container .menu span {
    width: 3rem;
    height: 4px;
    background: var(--weight-color);
    border-radius: 1rem;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
  }

  header .container .menu .span2 {
    width: 2rem;
  }

  .certificates .exit,
  .projects .exit,
  .contact .exit {
    top: 1%;
  }


}

@media (max-width:735px) {
  .loader img {
    /* width: 30% !important; */
  }

  header .links a {
    font-size: 1.5rem;
  }

  .social {
    display: none;
  }

  .home {
    min-height: 100vh;
  }

  .home .content .text-area {
    margin-bottom: 3rem;
  }

