    font-size: 28px;
  }

  .home .content .text-area .text p {
    font-size: 15px;
  }

  .home .content .text-area .text p {
    font-size: 15px;
  }

  .home .content .text-area .button {
    width: 200px;
    height: 50px;
  }

  .home .content .text-area .button a {
